const TextHover = styled(HyperLinkBold)`
  grid-area: downloadnow;
  cursor: pointer;
  margin: 0 auto;
  margin-top: 15px;
  position: relative;
  width: 210px;
  height: 32px;
  font-size: 20px;
  background: -webkit-linear-gradient(45deg, #ff278b 0%, #fca734 75%) 0% 0%
    no-repeat padding-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  :before {
    content: "";
    position: absolute;
    width: 0;
    height: 3px;
    bottom: 0;
    left: 0;
    background: -webkit-linear-gradient(45deg, #ff278b 0%, #fca734 75%) 0% 0%;
    visibility: hidden;
    transition: all 0.2s ease-in-out;
  }
  :hover:before {
    visibility: visible;
    width: 100%;
  }
`

